@import "../mixins";
.strategic-partner,
.about-develop{
  &__grid{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 0 1px;
  }
  &__img{
    width: 100%;
    display: block;
    min-height: 100%;
    object-fit: cover;
  }
}
.strategic-partner{
  padding: 110px 0;
  &__col{
    padding: 58px 56px;
    background-color: #333333;
    color: #fff;
    display: flex;
    flex-direction: column;
    text-transform: uppercase;
  }
  &__logo{
    width: 120px;
    display: block;
  }
  &__bottom{
    margin-top: auto;
    padding-top: 57px;
  }
  &__title{
    font-size: 26px;
    line-height: 135%;
    font-weight: 600;
    letter-spacing: .1em;
    max-width: 270px;
  }
  &__desc{
    font-size: 18px;
    line-height: 144%;
    letter-spacing: .05em;
    margin-top: 18px;
    @media screen and (min-width: 769px){
      @include truncate(5);
    }
  }

}

.about-advantages{
  &__inner{
    padding: 109px 0 82px;
    border-bottom: 1px solid #222222;
  }
  &__top{
    position: relative;
    &:before{
      content: '';
      position: absolute;
      bottom: 0;
      right: 0;
      width: 50%;
      border-bottom: 1px solid #222222;
    }
  }
  &__title{
    font-size: 32px;
    line-height: 43px;
    font-weight: 600;
    letter-spacing: .1em;
  }
  &__items{
    column-count: 2;
    margin-top: 62px;
    margin-bottom: -31px;
  }
}

.advantage-item{
  display: inline-flex;
  width: 100%;
  margin-bottom: 31px;
  font-weight: 600;
  text-transform: uppercase;
  &__num{
    font-size: 28px;
    width: 88px;
    flex-shrink: 0;
    span{
      color: #AAAAAA;
    }
  }
  &__content{
    font-size: 20px;
    line-height: 28px;
    max-width: 371px;
    letter-spacing: -.025em;
  }
}


@media screen and (max-width: 1200px){
  .strategic-partner{
    padding: 72px 0;
    &__col{
      padding: 55px 20px;
    }
  }
}


@media screen and (max-width: 992px){
  .about-advantages{
    &__inner{
      padding: 76px 0 0;
      border-bottom: none;
    }
    &__top{
      padding-bottom: 26px;
      &:before{
        width: 100%;
      }
    }
    &__title{
      font-size: 24px;
      line-height: 32px;
    }
    &__items{
      column-count: 1;
      margin-top: 38px;
    }
  }

}


@media screen and (max-width: 768px){
  .strategic-partner{
    &__grid{
      grid-template-columns: 100%;
    }
    &__title{
      font-size: 18px;
      max-width: 190px;
    }
    &__desc{
      font-size: 14px;
      line-height: 26px;
      margin-top: 31px;
    }
  }

  .advantage-item{
    &__num{
      font-size: 24px;
      line-height: 28px;
      width: 66px;
    }
    &__content{
      font-size: 16px;
      line-height: 26px;
      max-width: unset;
    }
  }
}


@media screen and (max-width: 576px){
  .strategic-partner{
    .container{
      padding: 0;
    }
  }
}
