@import "../mixins";
.about-services{
  padding-top: 100px;
  overflow: hidden;
  &__title{
    font-size: 28px;
    line-height: 37px;
    letter-spacing: .025em;
    font-weight: 600;
  }
}

.about-services-slider{
  margin-top: 35px;
  position: relative;
  .slider__wrap{
    overflow: hidden;
  }
  .swiper-container{
    width: calc(100% + 28px);
    overflow: unset;
  }
  .swiper-slide{
    width: 25%;
    padding-right: 28px;
  }
  .swiper-nav{
    position: absolute;
    bottom: 100%;
    right: 0;
    margin-bottom: 42px;
    display: flex;
  }
  .swiper-button{
    @include center;
    width: 33px;
    height: auto;
    cursor: pointer;
    padding: 0;
    position: static;
    margin: 0 0 0 20px;
    svg{
      width: 100%;
      height: auto;
    }
    &:after{
      display: none;
    }
    &:hover{
      path{
        stroke: #F5BD38;
      }
    }
  }
}

.service-card{
  &__img{
    @include imgCover(127%);
  }
  &__title{
    margin-top: 24px;
    text-align: center;
    font-size: 20px;
    line-height: 135%;
    letter-spacing: .1em;
    font-weight: 600;
    text-transform: uppercase;
  }
}

.about-desc{
  padding-top: 144px;
  &__title{
    font-size: 36px;
    line-height: 133%;
    font-weight: 600;
    text-transform: uppercase;
    letter-spacing: .075em;
  }
}

.about-develop{
  padding-top: 106px;
  &__col2{
    display: flex;
    justify-content: center;
    flex-direction: column;
    padding: 60px;
    background: #333333;
    color: #fff;
    text-transform: uppercase;
  }
  &__title{
    font-size: 31px;
    line-height: 36px;
    font-weight: 600;
    letter-spacing: .1em;
  }
  &__desc{
    font-size: 18px;
    line-height: 160%;
    letter-spacing: .05em;
    margin-top: 30px;
  }
}


@media screen and (max-width: 1300px){
  .about-services-slider{
    .swiper-slide{
      width: calc(100% / 3);
    }
  }
}


@media screen and (max-width: 1200px){
  .about-services-slider{
    .slider__wrap{
      overflow: unset;
    }
    .swiper-container{
      width: calc(100% + 20px);
    }
    .swiper-slide{
      width: 320px;
      padding-right: 20px;
      max-width: 80%;
    }
  }

  .service-card{
    &__title{
      margin-top: 23px;
      font-size: 18px;
      line-height: 22px;
    }
  }

  .about-develop{
    padding-top: 45px;
    &__col2{
      padding: 75px 20px;
    }
  }
}


@media screen and (max-width: 768px){
  .about-services{
    padding-top: 58px;
    &__title{
      font-size: 18px;
      line-height: 24px;
    }
  }

  .about-services-slider{
    margin-top: 16px;
    .swiper-nav{
      margin-bottom: 20px;
    }
    .swiper-button{
      width: 24px;
      margin-left: 15px;
    }
  }

  .about-desc{
    padding-top: 60px;
    &__title{
      font-size: 20px;
      line-height: 150%;
    }
  }

  .about-develop{
    &__grid{
      grid-template-columns: 100%;
    }
    &__title{
      font-size: 18px;
      line-height: 24px;
    }
    &__desc{
      font-size: 14px;
      line-height: 22px;
      margin-top: 28px;
    }
  }
}