.about-main{
  padding-top: 57px;
  border-top: 1px solid rgba(#707070,.2);
  &__title{
    font-size: 60px;
    line-height: 133%;
    font-weight: 600;
    max-width: 823px;
  }
  &__grid{
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin-top: 100px;
  }
  &__subtitle{
    font-size: 32px;
    line-height: 120%;
    font-weight: 600;
    text-transform: uppercase;
    max-width: 350px;
  }
  &__items{
    padding: 42px 0 66px;
    border-top: 1px solid #222222;
    border-bottom: 1px solid #222222;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 46px 5px;
  }
  &__item{
    &-num{
      font-size: 52px;
      line-height: 131%;
      font-weight: 600;
    }
    &-title{
      margin-top: 2px;
      font-size: 16px;
      line-height: 21px;
    }
  }
}

@media screen and (max-width: 1200px){
  .about-main{
    &__title{
      font-size: 45px;
      max-width: 600px;
    }
    &__grid{
      margin-top: 36px;
    }
    &__items{
      padding: 27px 0 34px;
      gap: 28px 3px;
    }
    &__item{
      &-num{
        font-size: 42px;
        line-height: 50px;
      }
      &-title{
        margin-top: 0;
        font-size: 13px;
        line-height: 17px;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .about-main{
    padding-top: 40px;
    border-top: none;
    &__grid{
      grid-template-columns: 100%;
      gap: 23px;
    }
    &__title{
      font-size: 28px;
      line-height: 34px;
      max-width: 450px;
    }
    &__subtitle{
      font-size: 20px;
      line-height: 24px;
      max-width: 230px;
    }
  }
}